import { Scene, Camera } from 'three'
import { VueConstructor } from 'vue'

import ArSceneType from './ArSceneType'
import MarkerMgr from './MarkerMgr'

import { assignWhenExist } from './ObjectHelper'
import Vue from 'vue'
import RangeDesc from './RangeDesc'

class ArScene extends Scene {
  sceneID = 'none'
  title = '请扫描带有AR标识的图片'
  sceneType = ArSceneType.none
  desc?: VueConstructor

  camera = new Camera()
  marker?: marker
  onTickFuncs: tickFunc[] = []

  params: arSceneParam[] = []

  paramsValue = new Vue({
    data: {
      values: {} as { [key: string]: any }
    }
  })

  constructor(marker?: marker) {
    super()
    this.visible = false
    this.marker = marker
    this.add(this.camera)
    this.camera.visible = false
  }

  setWithDesc(
    basicDesc: basicSceneDesc,
    markerMgr = MarkerMgr.instance
  ) {
    this.name = basicDesc.sceneID
    if (basicDesc.markerUrl)
      this.marker = markerMgr.get(basicDesc.markerUrl)
    let { sceneID, title, params, desc, onTickFuncs } = basicDesc
    if (params)
      for (let param of params)
        Vue.set(this.paramsValue.values, param.name,
          param.desc instanceof RangeDesc
            ? param.desc.initValue
            : param.desc.cases.get(param.desc.default)
        )
    assignWhenExist(this, { sceneID, title, params, desc, onTickFuncs })
  }

  getZoom() {
    return 1
  }

  static fromDesc(desc: basicSceneDesc): ArScene {
    let scene = new ArScene()
    scene.setWithDesc(desc)
    return scene
  }

}
export default ArScene