import { Geometry, BufferGeometry, Mesh } from 'three'

import ResourceMgr from './ResourceMgr'

import FileMgr from './FileMgr'
import { mapModels, importGLTF } from './ThreeJSHelper'

function isUrl(desc: modelSourceDesc): desc is { url: string } {
  return (desc as any).url
}

class ModelMgr
  extends ResourceMgr<modelSourceDesc, Model, modelDesc>
{
  fileMgr = FileMgr.instance

  regBasicRes(...descs: modelSourceDesc[]) {
    let _descs = descs.length ? descs : this.unready
    for (let desc of _descs)
      if (isUrl(desc))
        this.fileMgr.reg(desc.url)
  }

  async initBasicRes() {
    await this.fileMgr.init()
  }

  async make(desc: modelSourceDesc, basicReadied?: boolean) {
    if (typeof desc === 'string') return this.get(desc)
    if (
      desc instanceof Geometry ||
      desc instanceof BufferGeometry
    ) {
      this.set(desc, desc)
      return desc
    }
    if (!isUrl(desc)) {
      this.set(desc.name, desc.model)
      return desc.model
    }
    if (!basicReadied) {
      this.regBasicRes(desc)
      await this.initBasicRes()
    }
    let blob_url = this.fileMgr.getBlobUrl(desc.url)
    if (!blob_url) throw `Missed GLTF File: ${desc.url}`
    let gltf = await importGLTF(blob_url)
    console.log(gltf)
    mapModels(gltf.scene, this)
  }

  reg(desc: modelSourceDesc) {
    if (
      desc instanceof Geometry ||
      desc instanceof BufferGeometry
    ) this.set(desc, desc)
    else super.reg(desc)
  }

  static instance = new ModelMgr()

}

export default ModelMgr