import ResourceMgr from './ResourceMgr'

import FileMgr from './FileMgr'

class MarkerMgr extends ResourceMgr<string, marker> {
  fileMgr = FileMgr.instance

  regBasicRes(...descs: string[]) {
    let _descs = descs.length ? descs : this.unready
    for (let desc of _descs) {
      this.fileMgr.reg(`markers/${desc}.iset`)
      this.fileMgr.reg(`markers/${desc}.fset`)
      this.fileMgr.reg(`markers/${desc}.fset3`)
    }
  }

  async initBasicRes() {
    await this.fileMgr.init()
  }

  async make(desc: string, basicReadied?: boolean) {
    let inst = this.get(desc)
    if (inst) return inst
    if (!basicReadied) {
      this.regBasicRes(desc)
      await this.initBasicRes()
    }
    let iset = this.fileMgr.getBlobUrl(`markers/${desc}.iset`)
    let fset = this.fileMgr.getBlobUrl(`markers/${desc}.fset`)
    let fset3 = this.fileMgr.getBlobUrl(`markers/${desc}.fset3`)
    if (!iset || !fset || !fset3)
      throw `Error Marker: ${JSON.stringify(desc)}`
    let marker = { iset, fset, fset3 }
    this.set(desc, marker)
    return marker
  }

  static instance = new MarkerMgr()
}

export default MarkerMgr