import OrbitValue from './OrbitValue'

class OrbitController extends OrbitValue {
  nowState: 'none' | 'rotate' | 'zoom' = 'none'

  private startX = 0
  private startY = 0
  private startDist = 0
  private startValue = new OrbitValue()

  private updateState(event: TouchEvent | PointerEvent) {
    let changed = false
    let elem = event.srcElement as HTMLElement
    if (event instanceof PointerEvent)
      elem.setPointerCapture(event.pointerId)
    let touches = event instanceof TouchEvent
      ? event.touches : [event]
    if (touches.length === 1 && this.nowState !== 'rotate') {
      changed = true
      this.nowState = "rotate"
    }
    if (touches.length > 1 && this.nowState !== 'zoom') {
      changed = true
      this.nowState = "zoom"
    }
    if (changed) {
      elem.addEventListener('touchmove', this.touchMove_binded)
      elem.addEventListener('pointermove', this.touchMove_binded)
      elem.addEventListener('touchend', this.touchEnd_binded)
      elem.addEventListener('pointerup', this.touchEnd_binded)
      this.startX = touches[0].clientX
      this.startY = touches[0].clientY
      this.startDist = OrbitController.calcFingerDist(event)
      this.startValue.rotateX = this.rotateX
      this.startValue.rotateY = this.rotateY
      this.startValue.zoom = this.zoom
    }
    return changed
  }

  listenOn(elem: HTMLElement) {
    elem.addEventListener('touchstart', this.touchStart_binded)
    elem.addEventListener('pointerdown', this.touchStart_binded)
    elem.addEventListener('wheel', this.wheelScroll_binded)
  }

  unlistenOn(elem: HTMLElement) {
    elem.removeEventListener('touchstart', this.touchStart_binded)
    elem.removeEventListener('pointerdown', this.touchStart_binded)
    elem.removeEventListener('wheel', this.wheelScroll_binded)
  }

  wheelScroll(event: WheelEvent) {
    this.zoom = OrbitValue.ranges.zoom.fixValue(
      this.zoom - event.deltaY / 200
    )
  }

  wheelScroll_binded = this.wheelScroll.bind(this)

  touchStart(
    event: TouchEvent | PointerEvent, continueEvent = false
  ) {
    if (!continueEvent) event.preventDefault()
    this.updateState(event)
  }

  touchStart_binded = this.touchStart.bind(this)

  touchMove(
    event: TouchEvent | PointerEvent, continueEvent = false
  ) {
    if (!continueEvent) event.preventDefault()
    this.updateState(event)
    let touches = event instanceof TouchEvent
      ? event.touches : [event]
    if (this.nowState === "rotate") {
      this.rotateY = OrbitValue.ranges.rotateY.fixValue(
        this.startValue.rotateY - touches[0].clientX + this.startX
      )
      this.rotateX = OrbitValue.ranges.rotateX.fixValue(
        this.startValue.rotateX + touches[0].clientY - this.startY
      )
    } else                                                 // zoom
      this.zoom = OrbitValue.ranges.zoom.fixValue(
        this.startValue.zoom + (
          OrbitController.calcFingerDist(event) - this.startDist
        ) / 50
      )

  }

  touchMove_binded = this.touchMove.bind(this)

  touchEnd(event: TouchEvent | PointerEvent, continueEvent = false) {
    if (!continueEvent) event.preventDefault()
    let elem = event.srcElement as HTMLElement
    if (event instanceof PointerEvent)
      elem.releasePointerCapture(event.pointerId)
    elem.removeEventListener('touchmove', this.touchMove_binded)
    elem.removeEventListener('pointermove', this.touchMove_binded)
    elem.removeEventListener('touchend', this.touchEnd_binded)
    elem.removeEventListener('pointerup', this.touchEnd_binded)
    this.nowState = 'none'
  }

  touchEnd_binded = this.touchEnd.bind(this)

  static calcFingerDist(event: TouchEvent | MouseEvent) {
    if (
      event instanceof MouseEvent || event.touches.length < 2
    ) return 0
    let dx = event.touches[1].clientX - event.touches[0].clientX
    let dy = event.touches[1].clientY - event.touches[0].clientY
    return Math.sqrt(dx * dx + dy * dy)
  }

}

export default OrbitController