type WithChildren<T> = T & { children?: WithChildren<T>[] }

export function removeUndefProp<T>(obj: T): T {
  for (let key of Object.keys(obj))
    if ((obj as any)[key] === undefined)
      delete (obj as any)[key]
  return obj
}

export function assignWhenExist<T1, T2>(to: T1, from: T2): T1 & T2 {
  return Object.assign(to, removeUndefProp(from))
}

export function eachChild<T>(
  func: (
    item: WithChildren<T>,
    parent?: { children?: WithChildren<T>[] }
  ) => void,
  items: WithChildren<T>[],
  parent?: { children?: WithChildren<T>[] }
) {
  for (let item of items) {
    if (item.children) eachChild(func, item.children, item)
    func(item, parent)
  }
}

export function mapFindIndex<T_KEY, T>(
  _map: Map<T_KEY, T>,
  _value: T,
  _equalFunc = (a: T, b: T) => a === b
): T_KEY | undefined {
  for (let [key, value] of _map)
    if (_equalFunc(value, _value)) return key
}

export function mapFind<T>(
  _map: Map<unknown, T>,
  _value: T,
  _equalFunc = (a: T, b: T) => a === b
): T | undefined {
  for (let [, value] of _map)
    if (_equalFunc(value, _value)) return value
}
