import ResourceMgr from './ResourceMgr'
import IDBFS from './IDBFS'

class FileMgr extends ResourceMgr<string, Blob> {
  static fs = IDBFS.instance

  async make(desc: string): Promise<Blob | undefined> {
    let inst = this.get(desc)
    if (inst) return inst
    let blob = await FileMgr.fs.urlToBlob(desc)
    if (!blob) throw `Can't find file '${desc}' in FS!`
    this.set(desc, blob)
  }

  getBlobUrl(url: string) {
    let blob = this.get(url)
    return blob && FileMgr.fs.blobUrl(blob)
  }

  static instance = new FileMgr()
}

export default FileMgr