export async function tryAwait<T>(
  func: (...params: any[]) => Promise<T>,
  ...params: any[]
): Promise<T | void> {
  try {
    return await func(...params)
  } catch (e) { }
}

export async function sleep(ms: number) {
  return new Promise<number>(resolve => setTimeout(resolve, ms))
}

export function numsToStr(nums: number[], pad = 0, split = '') {
  return nums.map(n => n.toString().padStart(pad, '0')).join(split)
}

export function yyyymmdd(date?: Date, split = '') {
  if (!date) date = new Date()
  return numsToStr([
    date.getFullYear(), date.getMonth() + 1, date.getDate()
  ], 2, split)
}

export function hhmmss(date?: Date, split = '') {
  if (!date) date = new Date()
  return numsToStr([
    date.getHours(), date.getMinutes(), date.getSeconds()
  ], 2, split)
}