import {
  WebGLRenderer,
  PerspectiveCamera,
  PCFSoftShadowMap
} from 'three'

import ArModelScene from './ArModelScene'

class ModelRender extends WebGLRenderer {

  private _arScene: ArModelScene | null = null
  private _looper_binded = this._looper.bind(this)
  funcs: tickFunc[] = []
  running = false
  hidden = false
  lastTime = 0

  camera = new PerspectiveCamera()

  private _initRender() {
    this.setClearAlpha(0)
    this.setPixelRatio(window.devicePixelRatio)
    this.shadowMap.enabled = true
    this.shadowMap.type = PCFSoftShadowMap
  }

  private _initDom() {
    this.domElement.classList.add('filled')
    let thisOnResize = () => this._onResize()
    window.addEventListener('resize', thisOnResize)
  }

  constructor() {
    super({ antialias: true, alpha: true })
    this._initRender()
    this._initDom()
  }

  private _looper(time: number) {
    if (this.running) requestAnimationFrame(this._looper_binded)
    if (this.hidden) return this.clear()
    if (!this.lastTime) this.lastTime = time - 1000 / 60
    let delta = time - this.lastTime

    for (let func of this.funcs) func(delta, time)

    if (this._arScene) {
      for (let func of this._arScene.onTickFuncs)
        func(delta, time, this._arScene)
      this.render(this._arScene, this.camera)
    }

    this.lastTime = time
  }

  private _onResize() {
    if (!this.domElement.parentElement) return
    let w = this.domElement.parentElement.offsetWidth
    let h = this.domElement.parentElement.offsetHeight
    this.setSize(w, h)
    this.resetCamera()
  }

  resetCamera() {
    let w = this.domElement.offsetWidth
    let h = this.domElement.offsetHeight
    this.camera.aspect = w / h
    this.camera.fov = 70
    this.camera.updateProjectionMatrix()
  }

  start() {
    if (this.running) return
    this.hidden = false
    this.running = true
    requestAnimationFrame(this._looper_binded)
  }

  pause() {
    this.running = false
  }

  hide() {
    this.hidden = true
    this.pause()
  }

  domTo(container: HTMLElement) {
    this.setSize(container.offsetWidth, container.offsetHeight)
    container.appendChild(this.domElement)
    setInterval(() => this._onResize(), 2000)
  }

  get arScene() {
    return this._arScene
  }

  set arScene(arScene: ArModelScene | null) {
    if (this._arScene)
      this._arScene.visible = false
    if (arScene) {
      arScene.visible = true
      let s = 4 / arScene.size
      let content = arScene.getObjectByName('content')
      content && content.scale.set(s, s, s)
    }
    this._arScene = arScene
    this.resetCamera()
  }

}

export default ModelRender