import ArRender from './ArRender'
import ArSceneMgr from './ArSceneMgr'
import IDBFS from './IDBFS'
import IndexedDB from './IndexedDB'
import IndexFile from './IndexFile'
import ModelMgr from './ModelMgr'
import ResourceMgr from './ResourceMgr'
import ProcessEvent from './ProcessEvent'

type indexFileLoadFunc = (...params: any[]) => Promise<IndexFile>

class Loader {
  static fs = IDBFS.instance

  static models = ModelMgr.instance
  static scenes = ArSceneMgr.instance

  private static _cacheLoaded = false
  private static _resLoaded = false
  private static _arRender?: ArRender

  static get cacheLoaded() {
    return Loader._cacheLoaded
  }

  static get loaded() {
    return Loader._arRender !== undefined
  }

  static get render() {
    return Loader._arRender
  }

  static get resLoaded() {
    return Loader._resLoaded
  }

  static loadIndex: indexFileLoadFunc =
    async (url: RequestInfo, init?: RequestInit) => {
      return new IndexFile(await (await fetch(url, init)).text())
    }

  static loadCache: indexFileLoadFunc = Loader.fs.cachedIndex

  static async loadCacheFromDB(db: IndexedDB) {
    let result = new IndexFile()
    for await (let [{ name }, hash] of db.items('caches'))
      result.addData(hash.toString(), name)
    return result
  }

  static processCB?: processCB<any>

  private static emit(
    type: string,
    loaded = 0,
    total = 1,
    info?: any
  ) {
    if (Loader.processCB) Loader.processCB(
      new ProcessEvent(type, info, loaded, total)
    )
  }

  private static emitError(type: string, info?: any) {
    if (Loader.processCB) Loader.processCB(
      new ProcessEvent(type, info, 0, 1, false)
    )
  }

  static async updateCache() {
    if (Loader._cacheLoaded) return
    await Loader.fs.init()
    Loader.emit('Loader.IndexGetting', 1, 2)
    let index: IndexFile | undefined
    try {
      index = await Loader.loadIndex(IDBFS.resServer + '/index.txt')
    } catch (e) {
      Loader.emitError('Loader.IndexFetchError', e)
      let { versionName } = Loader.fs
      if (versionName)
        return Loader.emit('Loader.IndexLocal', 1, 1, { versionName })
      Loader.emitError('Loader.IndexNone')
      throw 'Init Caches Failed!'
    }
    Loader.emit('Loader.IndexDiffsCalcing', 2, 2)
    await Loader.fs.mergeTo(index, Loader.processCB)
    let { versionName } = Loader.fs
    Loader.emit('Loader.IndexNewest', 1, 1, { versionName })
    Loader._cacheLoaded = true
  }

  static async loadScenes(scenes: arSceneDesc[]) {
    if (Loader._resLoaded) return
    Loader.emit('Loader.ScenesReging')
    for (let url of Loader.fs.find(/models\//))
      Loader.models.reg({ url })
    for (let scene of scenes) ArSceneMgr.instance.reg(scene)
    Loader.emit('Loader.ScenesReged', 1)
    let initMgr = async (mgr: ResourceMgr<any, any>, t: string) => {
      mgr.processCB = (i, n) => Loader.emit(t, i, n)
      await mgr.init()
      delete mgr.processCB
    }
    await initMgr(Loader.models, 'Loader.ModelsLoading')
    await initMgr(Loader.scenes, 'Loader.ScenesLoading')
    Loader.emit('Loader.ScenesLoaded', 1)
    Loader._resLoaded = true
  }

  static async initAR(dom: HTMLElement, arSource?: arSourceDesc) {
    if (Loader._arRender) return Loader._arRender
    Loader.emit('Loader.ArIniting')
    let render = new ArRender(arSource)
    render.domTo(dom)
    await render.init()
    Loader.emit('Loader.ArInited', 1)
    return Loader._arRender = render
  }

  static async startAR() {
    if (!Loader._arRender) throw `Need initAR(dom) first!`
    let render = Loader._arRender
    if (!Loader._resLoaded) throw `Need loadResource(url) first!`
    await render.loadScenes(
      Array.from(Loader.scenes.values()), Loader.processCB)
    render.start()
    Loader.emit('Loader.ArStarted', 1)
  }

}

export default Loader