import HashInfo from './HashInfo'
import HashInfoDiffType from './HashInfoDiffType'
import { mapFind } from './ObjectHelper'

class HashInfoDiff<T = string> {
  left?: HashInfo<T>
  right?: HashInfo<T>
  type: HashInfoDiffType
  constructor(
    _type: HashInfoDiffType,
    _left?: HashInfo<T>,
    _right?: HashInfo<T>
  ) {
    this.left = _left
    this.right = _right
    this.type = _type
  }

  static toMap<T>(_arr: HashInfo<T>[]) {
    return new Map(_arr.map(item => [item.hash, item]))
  }

  static compare<T>(
    _left: HashInfo<T>[],
    _right: HashInfo<T>[],
  ): HashInfoDiff<T>[] {
    let diffs: HashInfoDiff<T>[] = []
    let map_left = HashInfoDiff.toMap(_left)
    let map_right = HashInfoDiff.toMap(_right)
    let reInfos: HashInfoDiff<T>[] = []
    for (let [right_hash, right_item] of map_right) {
      let left_item = map_left.get(right_hash)
      if (left_item) {
        // ReInfo Item
        if (!left_item.infoEqual(right_item))
          reInfos.push(new HashInfoDiff(
            HashInfoDiffType.ReInfo, left_item, right_item
          ))
        // UnChanged Item
        // else {}
        map_left.delete(right_hash)
        map_right.delete(right_hash)
      }
    }
    for (let [, left_item] of map_left) {
      let right_item = mapFind(map_right, left_item,
        (a, b) => a.infoEqual(b)
      )
      if (right_item) {                  // ReHash Item
        diffs.push(new HashInfoDiff(
          HashInfoDiffType.ReHash, left_item, right_item
        ))
        map_right.delete(right_item.hash)
      } else                            // Delete Item
        diffs.push(new HashInfoDiff(
          HashInfoDiffType.Delete, left_item
        ))
    }
    for (let [, right_item] of map_right) // Add Item
      diffs.push(new HashInfoDiff(
        HashInfoDiffType.Add, undefined, right_item
      ))
    return diffs.concat(reInfos)
  }

}

export default HashInfoDiff