import { Texture, TextureLoader } from 'three'

import ResourceMgr from './ResourceMgr'

import FileMgr from './FileMgr'

class TexMgr extends ResourceMgr<texDesc, Texture> {
  fileMgr = FileMgr.instance

  regBasicRes(...descs: texDesc[]) {
    let _descs = descs.length ? descs : this.unready
    for (let desc of _descs)
      if (!(desc instanceof Texture))
        this.fileMgr.reg(
          typeof desc === 'string' ? desc : desc.url
        )
  }

  async initBasicRes() {
    await this.fileMgr.init()
  }

  async make(desc: texDesc, basicReadied?: boolean) {
    let inst = this.get(desc)
    if (inst) return inst
    if (desc instanceof Texture) {
      this.set(desc, desc)
      return desc
    }
    let url = ''
    let params: any
    if (typeof desc === 'string') url = desc
    else {
      url = desc.url
      params = desc.params
    }
    if (!url) throw `Empty Texture Url!`
    if (!basicReadied) {
      this.regBasicRes(url)
      await this.initBasicRes()
    }
    let blob_url = this.fileMgr.getBlobUrl(url)
    if (!blob_url) throw `Missed Tex File: ${url}`
    let tex_loader = new TextureLoader()
    return new Promise<Texture>((resolve, reject) =>
      tex_loader.load(blob_url!, tex => {
        if (params) Object.assign(tex, params)
        this.set(desc, tex)
        resolve(tex)
      }, undefined, reject)
    )
  }

  reg(desc: texDesc) {
    if (desc instanceof Texture)
      this.set(desc, desc)
    else super.reg(desc)
  }

  static instance = new TexMgr()
}

export default TexMgr