import HashInfo from './HashInfo'
import IndexFile from './IndexFile'
import HashInfoDiff from './HashInfoDiff'
import IDBFSMergeStep from './IDBFSMergeStep'
import HashInfoDiffType from './HashInfoDiffType'

class IDBFSFile extends HashInfo<string[]> {

  constructor(hash: string, ...names: string[]) {
    super(hash, names)
  }

  infoEqualFunc(info: string[]): boolean {
    if (this.info.length !== info.length) return false
    this.info.sort()
    info.sort()
    for (let i = 0; i < info.length; i += 1)
      if (this.info[i] !== info[i]) return false
    return true
  }

  static fromIndex(indexFile: IndexFile): IDBFSFile[] {
    let map = new Map<string, string[]>()
    for (let [hash, name] of indexFile.datas) {
      if (!map.has(hash)) map.set(hash, [])
      map.get(hash)!.push(name)
    }
    let res: IDBFSFile[] = []
    for (let [hash, names] of map.entries())
      res.push(new IDBFSFile(hash, ...names))
    return res
  }

  static calcMergeSteps(
    from: IDBFSFile[], to: IDBFSFile[]
  ): IDBFSMergeStep {
    let diffs = HashInfoDiff.compare(from, to)
    let steps = new IDBFSMergeStep()
    let add = (diff: HashInfoDiff<string[]>) => {
      let { hash, info } = diff.right!
      steps.hashAdd.push({ hash, url: info[0] })
      steps.fileAdd.push(...info.map(url => ({ hash, url })))
    }
    let del = (diff: HashInfoDiff<string[]>) => {
      let { hash, info } = diff.left!
      steps.hashDelete.push(hash)
      steps.fileDelete.push(...info)
    }
    for (let diff of diffs)
      switch (diff.type) {
        case HashInfoDiffType.Add:
          add(diff)
          break
        case HashInfoDiffType.Delete:
          del(diff)
          break
        case HashInfoDiffType.ReInfo:
          let setLeft = new Set(diff.left!.info)
          let setRight = new Set(diff.right!.info)
          for (let item of setLeft) if (setRight.has(item))
            setLeft.delete(item), setRight.delete(item)
          steps.fileDelete.push(...setLeft)
          let { hash } = diff.left!
          for (let url of setRight) steps.fileAdd.push({ hash, url })
          break
        case HashInfoDiffType.ReHash:
          del(diff)
          add(diff)
      }
    return steps
  }

}

export default IDBFSFile