import { lerp, rlerp } from './MathHelper'

class NumberRange {
  max: number
  min: number

  get distance() {
    return this.max - this.min
  }

  constructor(min = 0, max = 1) {
    this.min = min
    this.max = max
  }

  clamp(n: number) {
    return n < this.min ? this.min : (
      n > this.max ? this.max : n
    )
  }

  contain(n: number) {
    return n >= this.min && n <= this.max
  }

  lerp(n: number) {
    return lerp(this.min, this.max, n)
  }

  rlerp(n: number) {
    return rlerp(this.min, this.max, n)
  }
}

export default NumberRange