// IndexedDB File System
import IndexedDB from './IndexedDB'
import IndexFile from './IndexFile'
import IDBFSFile from './IDBFSFile'
import ProcessEvent from './ProcessEvent'

class IDBFS {
  db?: IndexedDB
  private _initing = false
  private _version?: string
  private _versionName?: string

  blobUrlMap: Map<Blob, string> = new Map()
  urlBlobMap: Map<string, Blob> = new Map()
  files = new Set<string>()

  static resServer = ''
  static instance = new IDBFS()

  constructor() {
    this.init()
  }

  get initing() {
    return this._initing
  }

  get version() {
    return this._version || ''
  }

  get versionName() {
    return this._versionName || ''
  }

  private setVersion(version: string, versionName: string) {
    this._version = version
    this._versionName = versionName
  }

  private async updateVersion(version: string, versionName: string) {
    await this.db!.put('sets',
      { set_id: 'version', version, versionName, }
    )
    this.setVersion(version, versionName)
  }

  waiter?: (() => void)[]

  async init() {
    if (this.db) return
    if (this._initing) return new Promise<void>(resolve => {
      if (!this.waiter) this.waiter = []
      this.waiter.push(resolve)
    })
    this._initing = true
    this.db = await IndexedDB.open('IDBFS', 1, function (req) {
      IndexedDB.createFromTables(req.result, new Map([
        ['caches',
          { param: { keyPath: 'hash' } }
        ],
        ['files',
          {
            param: { keyPath: 'url' },
            indexs: [['hashIndex', 'hash', { unique: false }]]
          }
        ],
        ['sets',
          { param: { keyPath: 'set_id' } }
        ]
      ]))
    })
    await this.updateFiles()
    let ver = await this.db!.get('sets', 'version')
    if (ver) this.setVersion(ver.version, ver.versionName)
    if (this.waiter) this.waiter.forEach(resolve => resolve())
    this._initing = false
  }

  get inited() {
    return new Boolean(this.db).valueOf()
  }

  async updateFiles() {
    this.files.clear()
    let arr: string[] = []
    for await (let [{ url }] of this.db!.items('files')) arr.push(url)
    arr.sort().forEach(this.files.add, this.files)
  }

  static async fetchBlob(
    input: RequestInfo,
    init?: RequestInit | undefined
  ) {
    if (typeof input === 'string' && !input.startsWith('http'))
      input = IDBFS.resServer +
        (input.startsWith('/') ? input : '/' + input)
    return (await fetch(input, init)).blob()
  }

  async regUrl(url: string, hash: string, overwrite?: boolean) {
    if (overwrite) return this.db!.put('files', { url, hash })
    return this.db!.add('files', { url, hash })
  }

  async regBlob(hash: string, blob: Blob) {
    await this.init()
    return this.db!.put('caches', { hash, blob })
  }

  async hashToBlob(hash: string): Promise<Blob | undefined> {
    return (await this.db!.get('caches', hash)).blob
  }

  async hashToUrl(hash: string): Promise<string[]> {
    return this.db!.findKeys('files', hash, 'hashIndex') as any
  }

  async urlToHash(url: string): Promise<string | undefined> {
    return (await this.db!.get('files', url)).hash
  }

  async urlToBlob(url: string): Promise<Blob | undefined> {
    let hash = await this.urlToHash(url)
    if (hash) return this.hashToBlob(hash)
  }

  async urlToBlobUrl(url: string): Promise<string | undefined> {
    let blob = await this.urlToBlob(url)
    return blob && this.blobUrl(blob)
  }

  blobUrl(blob: Blob): string {
    let url = this.blobUrlMap.get(blob)
    if (!url) {
      url = URL.createObjectURL(blob)
      this.blobUrlMap.set(blob, url)
      this.urlBlobMap.set(url, blob)
    }
    return url
  }

  blobUrlToBlob(url: string) {
    return this.urlBlobMap.get(url)
  }

  async hasUrl(url: string): Promise<boolean> {
    return this.db!.has('files', url)
  }

  async delHash(hash: string, tryClean?: boolean) {
    await this.db!.del('caches', hash)
    if (tryClean)
      for (let url of await this.hashToUrl(hash))
        await this.delUrl(url)
  }

  async delUrl(url: string, tryClean?: boolean) {
    let hash: string | undefined
    if (tryClean) hash = await this.urlToHash(url)
    await this.db!.del('files', url)
    if (hash && !await this.db!.has('files', hash, 'hashIndex'))
      await this.delHash(hash)
  }

  async rename(url: string, newUrl: string, overwrite = false) {
    let hash = await this.urlToHash(url)
    if (!hash) throw `Can't find url: ${hash}`
    await this.regUrl(newUrl, hash, overwrite)
    await this.delUrl(url)
  }

  async cachedIndex() {
    let cached = new IndexFile()
    cached.version = this.version
    cached.versionName = this.versionName
    for await (let [{ hash, url }] of this.db!.items('files'))
      cached.addData(hash, url)
    return cached
  }

  async calcDiffs(indexFile: IndexFile) {
    if (this.version === indexFile.version) return
    return IDBFSFile.calcMergeSteps(
      IDBFSFile.fromIndex(await this.cachedIndex()),
      IDBFSFile.fromIndex(indexFile)
    )
  }

  find(rule: RegExp) {
    return [...this.files].filter(url => rule.test(url))
  }

  async mergeTo(
    indexFile: IndexFile,
    processCB?: processCB<{ action: string, content: string }>
  ) {
    let diffs = await this.calcDiffs(indexFile)
    if (!diffs) return
    let n = 0, total = diffs.length
    let proc = (action: string, content: string) =>
      processCB && processCB(new ProcessEvent(
        'IDBFS.Merge', { action, content }, n += 1, total
      ))
    for (let { hash, url } of diffs.hashAdd) {
      await proc('下载', url)
      await this.regBlob(hash, await IDBFS.fetchBlob(url))
    }
    for (let url of diffs.fileDelete) {
      await proc('解注册', url)
      await this.delUrl(url)
    }
    for (let { hash, url } of diffs.fileAdd) {
      await proc('注册', url)
      await this.regUrl(url, hash)
    }
    for (let hash of diffs.hashDelete) {
      await proc('删除', hash)
      await this.delHash(hash)
    }
    await this.updateFiles()
    let { version, versionName } = indexFile
    await this.updateVersion(version, versionName)
  }

}

export default IDBFS