import { Object3D, Material, Mesh, Scene, Group } from 'three'

import { GLTFLoader, GLTF } from 'three/examples/jsm/loaders/GLTFLoader'

import MatMgr from './MatMgr'
import ModelMgr from './ModelMgr'
import { toRads } from './MathHelper'

import ProcessEvent from './ProcessEvent'

export function meshesFromDescs(
  descs: modelItemDesc[],
  parent?: Object3D,
  overrideMat?: Material,
  modelMgr = ModelMgr.instance,
  matMgr = MatMgr.instance,
) {
  return descs.map(desc => meshFromDesc(
    desc, parent, overrideMat, modelMgr, matMgr
  ))
}

export function meshFromDesc(
  desc: modelItemDesc,
  parent?: Scene | Object3D,
  overrideMat?: Material,
  modelMgr = ModelMgr.instance,
  matMgr = MatMgr.instance,
): Mesh {
  let model = modelMgr.get(desc.model)
  let mat = desc.mat && matMgr.get(desc.mat)
  let mesh = new Mesh(model, overrideMat || mat)
  if (mat) mesh.userData['_mat'] = mat
  if (parent) parent.add(mesh)
  if (desc.name) mesh.name = desc.name
  if (desc.position) mesh.position.set(...desc.position)
  if (desc.rotate) mesh.rotation.set(...<xyz>toRads(...desc.rotate))
  if (desc.scale) mesh.scale.set(...desc.scale)
  if (desc.children) meshesFromDescs(
    desc.children, mesh, overrideMat, modelMgr, matMgr
  )
  mesh.castShadow = true
  mesh.receiveShadow = true
  return mesh
}

export function isMesh(obj: Object3D): obj is Mesh {
  return obj.type === 'Mesh'
}

export function mapModels(
  obj: Object3D,
  to?: Map<any, Model>,
  cleanMat = true,
) {
  let result = to || new Map<string, Model>()
  obj.traverse(child => {
    if (!isMesh(child)) return
    result.set(child.name, child.geometry)
    if (!cleanMat || !child.material) return
    if (Array.isArray(child.material))
      for (let mat of child.material) mat.dispose()
    else child.material.dispose()
  })
  return result
}

export async function importGLTF(
  url: string, onProgress?: processCB<any>
) {
  let cb = onProgress && ((ev: ProgressEvent) => onProgress(
    new ProcessEvent('GLTFLoading', ev, ev.loaded, ev.total)
  ))
  return new Promise<GLTF>((resolve, reject) =>
    new GLTFLoader().load(url, resolve, cb, reject)
  )
}
