class HashInfo<T = string> {
  hash: string
  info: T
  constructor(_hash: string, _info: T) {
    this.hash = _hash
    this.info = _info
  }

  // When the T is object, maybe need overwrite this func
  infoEqualFunc(info: T): boolean {
    return this.info === info
  }

  infoEqual(another: HashInfo<T>): boolean {
    return this.infoEqualFunc(another.info)
  }

  static toMap<T>(array: Iterable<HashInfo<T>>): Map<string, T> {
    let map = new Map<string, T>()
    for (let item of array) map.set(item.hash, item.info)
    return map
  }

  static fromMap<T>(map: Map<string, T>): HashInfo<T>[] {
    let arr: HashInfo<T>[] = []
    for (let [hash, info] of map) arr.push(new HashInfo(hash, info))
    return arr
  }
}

export default HashInfo