
class IndexedDB {

  readonly idb: IDBDatabase

  constructor(_idb: IDBDatabase) {
    this.idb = _idb
  }

  static createFromTables(db: IDBDatabase, tables: IDBTables) {
    for (let [name, opts] of tables)
      if (!db.objectStoreNames.contains(name)) {
        let store = db.createObjectStore(name, opts.param)
        if (opts.indexs)
          for (let index of opts.indexs)
            store.createIndex(...index)
      }
  }

  static async open(
    name: string,
    version: number,
    updateFunc: IDBVersionUpdateFunc,
  ) {
    return new Promise<IndexedDB>(function (resolve, reject) {
      let req = window.indexedDB.open(name, version)
      req.onsuccess = function () {
        resolve(new IndexedDB(this.result))
      }
      req.onerror = reject
      req.onupgradeneeded = function (ev) {
        updateFunc(this, ev)
      }
    })
  }

  getTable(table: string, readonly = false) {
    return this.idb.transaction(
      table, readonly ? "readonly" : "readwrite"
    ).objectStore(table)
  }

  private static async reqToPromise<T>(req: IDBRequest<T>) {
    return new Promise<T>(function (resolve, reject) {
      req.onerror = reject
      req.onsuccess = function () { resolve(this.result) }
    })
  }

  async add(table: string, data: any) {
    return IndexedDB.reqToPromise(this.getTable(table).add(data))
  }

  async put(table: string, data: any) {
    return IndexedDB.reqToPromise(this.getTable(table).put(data))
  }

  async findKey(
    table: string, key: IDBKey, indexName?: string
  ): Promise<IDBValidKey | undefined> {
    return IndexedDB.reqToPromise(
      indexName
        ? this.getTable(table).index(indexName).getKey(key)
        : this.getTable(table).getKey(key)
    )
  }

  async findKeys(
    table: string, key: IDBKey, indexName?: string
  ): Promise<IDBValidKey[]> {
    if (!indexName) return await this.has(table, key) ?
      [key as IDBValidKey] : []
    return IndexedDB.reqToPromise(
      this.getTable(table).index(indexName).getAllKeys(key)
    )
  }

  async del(table: string, key: IDBKey, indexName?: string) {
    let _key = await this.findKey(table, key, indexName)
    if (_key === undefined) return
    await IndexedDB.reqToPromise(this.getTable(table).delete(_key))
    return _key
  }

  static dataToKey(table: IDBObjectStore, data: any): IDBKey {
    let { keyPath } = table
    if (typeof keyPath === 'string') return data[keyPath]
    return keyPath.map(value => data[value])
  }

  async has(table: string, key: IDBKey, indexName?: string) {
    return (await this.findKey(table, key, indexName)) !== undefined
  }

  async get(table: string, key: IDBKey, indexName?: string) {
    let _key = await this.findKey(table, key, indexName)
    return _key &&
      await IndexedDB.reqToPromise(this.getTable(table).get(_key))
  }

  async* items(
    table: IDBObjectStore | string
  ): AsyncGenerator<[any, IDBKey, number, number]> {
    if (typeof table === 'string') table = this.getTable(table)
    let total = await IndexedDB.reqToPromise(table.count())
    let req = table.openCursor()
    let n = 0, cursor: IDBCursorWithValue | null
    while (cursor = await IndexedDB.reqToPromise(req)) {
      yield [cursor.value, cursor.key, n, total]
      n += 1
      cursor.continue()
    }
  }

}

export default IndexedDB