import {
  Light,
  DirectionalLight,
  CircleBufferGeometry,
  Mesh,
  ShadowMaterial,
  MeshNormalMaterial,
  MeshStandardMaterial,
  HemisphereLight,
  PointLight,
  Group,
  Vector3,
} from 'three'

import ArScene from './ArScene'

import ArSceneType from './ArSceneType'
import MarkerMgr from './MarkerMgr'
import MatMgr from './MatMgr'
import TexMgr from './TexMgr'
import ModelMgr from './ModelMgr'
import OrbitValue from './OrbitValue'

import { assignWhenExist, eachChild } from './ObjectHelper'
import { toRad } from './MathHelper'
import { meshesFromDescs, meshFromDesc } from './ThreeJSHelper'

class ArModelScene extends ArScene {

  size = 1

  actions: arSceneAction[] = []

  center = new Vector3(0, 0, 0)

  get renderShadow() {
    return this.shadowLight.castShadow
  }

  set renderShadow(value: boolean) {
    this.shadowLight.castShadow = value
  }

  shipBody?: Mesh

  lights = {
    easy: [
      new DirectionalLight(undefined, 0.5),
      new DirectionalLight(undefined, 0.5),
    ],
    on: [
      new DirectionalLight(undefined, 0.4),
      new PointLight(undefined, 0.3),
      new DirectionalLight(undefined, 0.2),
      new PointLight(undefined, 0.1),
    ],
    off: []
  }

  basicLight = new HemisphereLight(0xa5e0ff, 0x6a5327, 1)
  shadowLight = new DirectionalLight(0xffffff, 0.2)

  groundMat = new ShadowMaterial({ opacity: 0.5 })

  ground = new Mesh(
    new CircleBufferGeometry(1000, 6),
    this.groundMat
  )

  private _mat: matType = 'origin'

  get mat() {
    return this._mat
  }

  set mat(value: matType) {
    this._mat = value
    this.updateMat()
  }

  static normalMat = new MeshNormalMaterial()

  static colorMat = new MeshStandardMaterial({
    color: 0x87c7ee,
    metalness: 0.2,
    roughness: 0.55,
  })

  updateMat() {
    if (this._mat !== 'origin') {
      let mat = this._mat === 'color'
        ? ArModelScene.colorMat
        : ArModelScene.normalMat
      eachChild(item => {
        if (item instanceof Mesh)
          item.material = mat
      }, this.children)
    } else
      eachChild(item => {
        if (item instanceof Mesh)
          item.material =
            item.userData['_mat'] || ArModelScene.colorMat
      }, this.children)
    this.ground.material = this.groundMat
  }

  private _light: lightType = 'on'

  get light() {
    return this._light
  }

  set light(value: lightType) {
    this.remove(...this.lights[this._light])
    this._light = value
    this._updateLights()
  }

  private _orbitLights(lights: Light[], startAngle = 33.7) {
    let orbit = new OrbitValue()
    orbit.farRange.max = this.lightRadius
    orbit.rotateX = 30
    let n = lights.length
    for (let i = 0; i < n; i += 1) {
      orbit.rotateY = startAngle + i * 360 / n
      lights[i].position.set(...orbit.cameraPosition)
    }
  }

  private _updateEasyLights() {
    this._orbitLights(this.lights.easy)
  }

  private _updateComplexLights() {
    this._orbitLights(this.lights.on)
  }

  private _updateLights() {
    this.add(...this.lights[this._light])
    if (this._light === 'off')
      this.basicLight.intensity = 1.5
    else {
      this.basicLight.intensity = 0.8
      if (this._light === 'easy')
        this._updateEasyLights()
      else
        this._updateComplexLights()
    }
  }

  private _lightRadius = 2

  get lightRadius(): number {
    return this._lightRadius
  }

  set lightRadius(value: number) {
    this._lightRadius = value
    this._updateLights()
  }

  private _initLight() {
    this.basicLight.name = '_basicLight'
    this.add(this.basicLight)
    this.shadowLight.name = '_shadowLight'
    this.shadowLight.position.set(5, 10, 7.5)
    this.shadowLight.shadow.mapSize.set(2048, 2048)
    this.shadowLight.castShadow = true
    this.add(this.shadowLight)
    this.lights.easy.forEach(
      (item, index) => item.name = `_easyLight${index}`
    )
    this.lights.on.forEach(
      (item, index) => item.name = `_light${index}`
    )
    this._updateLights()
  }

  private _initGround() {
    this.ground.name = "_ground"
    this.ground.rotation.set(toRad(-90), 0, 0)
    this.ground.receiveShadow = true
    this.add(this.ground)
  }

  constructor(marker?: marker) {
    super(marker)
    this.sceneType = ArSceneType.model
    this._initGround()
    this._initLight()
  }

  static fromDesc(
    desc: modelSceneDesc,
    markerMgr = MarkerMgr.instance,
    modelMgr = ModelMgr.instance,
    matMgr = MatMgr.instance,
    texMgr = TexMgr.instance,
  ): ArModelScene {
    let scene = new ArModelScene()
    scene.setWithDesc(desc, markerMgr)
    let overrideMat = {
      origin: undefined,
      color: ArModelScene.colorMat,
      normal: ArModelScene.normalMat,
    }[scene.mat]
    let { size, actions } = desc
    assignWhenExist(scene, { size, actions })
    if (desc.center)
      scene.center = new Vector3(...desc.center)
    if (desc.env)
      scene.environment = texMgr.get(desc.env) || null
    if (desc.children) {
      let content = new Group()
      content.name = 'content'
      meshesFromDescs(
        desc.children, content, overrideMat, modelMgr, matMgr
      )
      scene.add(content)
    }
    if (desc.shipBody)
      scene.shipBody = meshFromDesc(
        desc.shipBody, undefined, overrideMat, modelMgr, matMgr
      )
    return scene
  }
}

export default ArModelScene