import { Vector3 } from 'three'

export function toRad(deg: number) {
  return deg / 180 * Math.PI
}

export function toRads(...degs: number[]) {
  return degs.map(deg => toRad(deg))
}

export function toDegree(rad: number) {
  return rad * 180 / Math.PI
}

export function toDegrees(...rads: number[]) {
  return rads.map(rad => toDegree(rad))
}

export function lerp(a: number, b: number, v: number) {
  return (b - a) * v + a
}

export function rlerp(a: number, b: number, v: number) {
  return (v - a) / (b - a)
}

export function circleOut(v: number) {
  if (v < 0) v = 0
  return Math.sqrt(1 - Math.pow(v - 1, 2))
}

export function powerOut(v: number, pow = 2) {
  if (v > 1) return 1
  return 1 - Math.pow((1 - v), pow)
}

export function stick(value: number, to = 0, ease = 50) {
  let delta = value - to
  return Math.sign(delta) * Math.log1p(Math.abs(delta) / ease) * ease + to
}

export function distance(x1: number, y1: number, x2: number, y2: number) {
  let dx = x2 - x1
  let dy = y2 - y1
  return Math.sqrt(dx * dx + dy * dy)
}

export function toZero(value: number, delta: number) {
  let result
  if (value > 0) {
    result = value - delta
    return result < 0 ? 0 : result
  } else {
    result = value + delta
    return result > 0 ? 0 : result
  }
}

export function newEaseFunc(k: number = 0.2) {
  let b = k * k / (1 - k - k)
  if (b === Infinity) return function (x: number) { return x }
  let a = b + b * b
  return function (x: number) {
    return 1 + b - a / (x + b)
  }
}

export function toVec3(xyz: xyz) {
  return new Vector3(...xyz)
}

export function toXYZ(vec3: Vector3): xyz {
  return [vec3.x, vec3.y, vec3.z]
}