import NumberRange from './NumberRange'
import RangeDesc from './RangeDesc'

import { circleOut, toRad } from './MathHelper'
import { Vector3 } from 'three'

class OrbitValue {
  rotateX = OrbitValue.ranges.rotateX.initValue
  rotateY = OrbitValue.ranges.rotateY.initValue
  zoom = OrbitValue.ranges.zoom.initValue

  center = new Vector3(0, 0, 0)

  farRange = new NumberRange(1, 10)

  get isInitValue() {
    return this.rotateX === OrbitValue.ranges.rotateX.initValue &&
      this.rotateY === OrbitValue.ranges.rotateY.initValue &&
      this.zoom === OrbitValue.ranges.zoom.initValue
  }

  get cameraPosition(): xyz {
    let r = OrbitValue.zoomToR(this.farRange, this.zoom)
    let y = r * Math.sin(toRad(this.rotateX))
    let r1 = r * Math.cos(toRad(this.rotateX))
    let x = r1 * Math.sin(toRad(this.rotateY))
    let z = r1 * Math.cos(toRad(this.rotateY))
    return [
      x + this.center.x,
      y + this.center.y,
      z + this.center.z,
    ]
  }

  reset() {
    Object.assign(this, OrbitValue.initValue)
  }

  static zoomRange = new NumberRange(1, 10)

  static zoomToR(farRange: NumberRange, zoom: number) {
    return farRange.lerp(
      1 - circleOut(OrbitValue.zoomRange.rlerp(zoom))
    )
  }

  static ranges = {
    rotateX: RangeDesc.create({
      min: -90,
      max: 90,
      step: 1,
      group: 5,
      unit: "°",
      initValue: 30
    }),
    rotateY: RangeDesc.create({
      min: -180,
      max: 180,
      step: 1,
      group: 10,
      unit: "°",
      initValue: 0
    }),
    zoom: RangeDesc.create({
      min: 1,
      max: 10,
      step: 0.1,
      group: 10,
      unit: "x",
      initValue: 1,
    })
  };

  static get initValue() {
    return {
      rotateX: OrbitValue.ranges.rotateX.initValue,
      rotateY: OrbitValue.ranges.rotateY.initValue,
      zoom: OrbitValue.ranges.zoom.initValue,
    }
  }
}

export default OrbitValue