class IDBFSMergeStep {
  hashDelete: string[] = []
  hashAdd: {
    hash: string,
    url: string
  }[] = []
  fileDelete: string[] = []
  fileAdd: {
    hash: string,
    url: string
  }[] = []
  get length() {
    return this.hashDelete.length
      + this.hashAdd.length
      + this.fileDelete.length
      + this.fileAdd.length
  }
}

export default IDBFSMergeStep