




import { Component, Vue, Prop, Watch } from 'vue-property-decorator'

import ArModelScene from '../../ArModelScene'
import ModelRender from '../../ModelRender'
import OrbitController from '../../OrbitController'
import NumberRange from '../../NumberRange'

@Component
export default class ModelViewer extends Vue {
  nowScene?: ArModelScene
  modelRender?: ModelRender
  @Prop({ default: true }) orbitView!: boolean
  @Prop({ default: 'on' }) light!: lightType
  @Prop({ default: 'origin' }) mat!: matType
  @Prop({ default: true }) shadow!: boolean
  viewController = new OrbitController()

  mounted() {
    this.modelRender = new ModelRender()
    let elem = this.$refs['model-render'] as HTMLElement
    this.modelRender.domTo(elem)
    this.orbitViewChanged()
    this.cameraPosChange(this.viewController.cameraPosition)
    this.modelRender.start()
  }

  beforeDestroy() {
    this.modelRender!.pause()
  }

  useScene(scene: ArModelScene) {
    this.nowScene = scene
    this.modelRender!.arScene = scene
    this.viewController.center = scene.center
    this.lightChanged()
    this.matChanged()
  }

  @Watch('light') lightChanged() {
    if (this.nowScene) this.nowScene.light = this.light
  }

  @Watch('mat') matChanged() {
    if (this.nowScene) this.nowScene.mat = this.mat
  }

  @Watch('shadow') shadowChanged() {
    if (this.nowScene) this.nowScene.renderShadow = this.shadow
  }

  @Watch('orbitView') orbitViewChanged() {
    let elem = this.$refs['model-render'] as HTMLElement
    if (this.orbitView) this.viewController.listenOn(elem)
    else this.viewController.unlistenOn(elem)
  }

  @Watch('viewController.cameraPosition') cameraPosChange(pos: xyz) {
    this.modelRender!.camera.position.set(...pos)
    this.modelRender!.camera.lookAt(0, 0, 0)
  }
}
