class IndexFile {
  version = ''
  versionName = ''
  generatorName = 'HELPER'
  splitBar = '-'.repeat(70)
  lineSeparator = '\n'
  columnseparator = '\t'
  datas: string[][] = []

  constructor(text?: string) {
    if (text) this.text = text
  }

  get dataText(): string {
    return this.datas.map(
      data => data.join(this.columnseparator)
    ).join(this.lineSeparator)
  }

  set dataText(text: string) {
    this.parseData(text.split(this.lineSeparator))
  }

  parseData(lines: string[]) {
    this.cleanData()
    this.datas = lines.map(line => line.split(this.columnseparator))
  }

  get headerText(): string {
    return [
      '',
      `THIS FILE IS GENERATED BY ${this.generatorName}, DON'T CHANGE IT MANUALLY!`,
      '',
      [this.version, this.versionName].join(this.columnseparator)
    ].join(this.lineSeparator)
  }

  set headerText(text: string) {
    this.parseHeader(text.split(this.lineSeparator))
  }

  parseHeader(lines: string[]) {
    this.generatorName =
      lines[1].substring(26, lines[1].length - 27);
    [this.version, this.versionName] =
      lines[3].split(this.columnseparator)
    if (this.headerText !== lines.join(this.lineSeparator))
      throw 'Error IndexFile Header!'
  }

  get text(): string {
    let result = this.headerText + this.lineSeparator + this.splitBar
    if (this.datas.length)
      result += this.lineSeparator + this.dataText
    return result + this.lineSeparator
  }

  set text(text: string) {
    this.parseText(
      text.replace(/\r\n/g, '\n').split(this.lineSeparator)
    )
  }

  parseText(lines: string[]) {
    this.parseHeader(lines.slice(0, 4))
    this.parseData(lines.slice(5, -1))
  }

  addData(...columns: string[]) {
    this.datas.push(columns)
  }

  cleanData() {
    this.datas = []
  }

}

export default IndexFile