class ProcessEvent<T> extends ProgressEvent {

  static msgs = new Map<string, (ev: ProcessEvent<any>) => string>([
    [
      'IDBFS.Merge', (
        ev: ProcessEvent<{ action: string, content: string }>
      ) => `正在${ev.info.action}资源：${ev.info.content}`
    ],
    [
      'Loader.IndexGetting',
      () => '正在下载资源列表…'
    ],
    [
      'Loader.IndexFetchError',
      ev => `下载资源列表失败：${ev.info}`
    ],
    [
      'Loader.IndexLocal', (
        ev: ProcessEvent<{ versionName: string }>
      ) => `使用本地资源版本：${ev.info.versionName}`
    ],
    [
      'Loader.IndexNone',
      () => '本地资源库尚未初始化完成，请联网后重试！'
    ],
    [
      'Loader.IndexDiffsCalcing',
      () => '正在比对资源差异…'
    ],
    [
      'Loader.IndexNewest', (
        ev: ProcessEvent<{ versionName: string }>
      ) => `资源已是最新版本：${ev.info.versionName}`
    ],
    [
      'Loader.ScenesReging',
      () => '正在注册场景信息…'
    ],
    [
      'Loader.ScenesReged',
      () => '场景信息注册完成'
    ],
    [
      'Loader.ModelsLoading',
      () => '正在加载模型…'
    ],
    [
      'Loader.ScenesLoading',
      () => '正在加载场景…'
    ],
    [
      'Loader.ScenesLoaded',
      () => '场景加载完成'
    ],
    [
      'Loader.ArIniting',
      () => '正在初始化AR环境…'
    ],
    [
      'Loader.ArInited',
      () => 'AR环境初始化完成'
    ],
    [
      'ArRender.ArStarting',
      () => '正在启动AR…'
    ],
    [
      'Loader.ArStarted',
      () => 'AR启动成功'
    ],
  ])

  info: T

  constructor(
    type: string,
    info: T,
    loaded = 0,
    total = 1,
    lengthComputable = true
  ) {
    super(type, { total, loaded, lengthComputable })
    this.info = info
    let m = ProcessEvent.msgs.get(type)
    this.msgTemplete = m
    if (m) this.msg = m(this)
  }

  msgTemplete?: (ev: ProcessEvent<T>) => string

  msg = ''

  static newError<T>(
    type: string,
    info: T,
    loaded = 0,
    total = 1
  ) {
    return new ProcessEvent(type, info, loaded, total, false)
  }

}

export default ProcessEvent