import NumberRange from './NumberRange'

class RangeDesc extends NumberRange {
  group = 10
  initValue = 0
  step = 1
  unit = ''

  fixValue(val: number): number {
    let v = Math.round(
      (val - this.min) / this.step
    ) * this.step + this.min
    return this.clamp(v)
  }

  static create(params?: {
    group?: number,
    initValue?: number,
    min?: number,
    max?: number,
    step?: number,
    unit?: string,
  }) {
    let result = new RangeDesc()
    Object.assign(result, params)
    return result
  }
}

export default RangeDesc