import ResourceMgr from './ResourceMgr'

import ArExpScene from './ArExpScene'
import ArModelScene from './ArModelScene'
import ArScene from './ArScene'
import ArSceneType from './ArSceneType'
import ArVideoScene from './ArVideoScene'
import FileMgr from './FileMgr'
import MarkerMgr from './MarkerMgr'
import MatMgr from './MatMgr'
import ModelMgr from './ModelMgr'

import { eachChild } from './ObjectHelper'

class ArSceneMgr extends ResourceMgr<arSceneDesc, ArScene, string> {
  markerMgr = MarkerMgr.instance
  modelMgr = ModelMgr.instance
  matMgr = MatMgr.instance
  fileMgr = FileMgr.instance

  static descIsName(desc: arSceneDesc): desc is string {
    return typeof desc === 'string'
  }

  static descIsModel(desc: arSceneDesc): desc is modelSceneDesc {
    return !ArSceneMgr.descIsName(desc) &&
      desc.sceneType === ArSceneType.model
  }

  static descIsVideo(desc: arSceneDesc): desc is videoSceneDesc {
    return !ArSceneMgr.descIsName(desc) &&
      desc.sceneType === ArSceneType.video
  }

  static descIsExp(desc: arSceneDesc): desc is expSceneDesc {
    return !ArSceneMgr.descIsName(desc) &&
      desc.sceneType === ArSceneType.exp
  }

  static descIsNone(desc: arSceneDesc): desc is expSceneDesc {
    return !ArSceneMgr.descIsName(desc) &&
      desc.sceneType === ArSceneType.none
  }

  private async _makeScene(
    desc: basicSceneDesc,
    basicReadied?: boolean,
    method = ArScene.fromDesc,
  ) {
    if (!basicReadied) {
      this.regBasicRes(desc)
      await this.initBasicRes()
    }
    let scene = method(desc)
    this.set(scene.name, scene)
    return scene
  }

  async make(
    desc: arSceneDesc,
    basicReadied?: boolean
  ): Promise<ArScene | void> {
    if (ArSceneMgr.descIsName(desc))
      return this.get(desc)
    let inst = this.get(desc.sceneID)
    if (inst) return inst
    if (ArSceneMgr.descIsModel(desc))
      return this._makeScene(
        desc, basicReadied, ArModelScene.fromDesc)
    else if (ArSceneMgr.descIsVideo(desc))
      return this._makeScene(
        desc, basicReadied, ArVideoScene.fromDesc)
    else if (ArSceneMgr.descIsExp(desc))
      return this._makeScene(
        desc, basicReadied, ArExpScene.fromDesc)
    else
      return this._makeScene(
        desc, basicReadied) // descIsNone
  }

  regBasicRes_basic(desc: basicSceneDesc) {
    if (desc.markerUrl) this.markerMgr.reg(desc.markerUrl)
  }

  regBasicRes_model(desc: modelSceneDesc) {
    this.regBasicRes_basic(desc)
    if (desc.children)
      eachChild(desc_model => {
        this.modelMgr.reg(desc_model.model)
        if (desc_model.mat) this.matMgr.reg(desc_model.mat)
      }, desc.children, desc)
  }

  regBasicRes_video(desc: videoSceneDesc) {
    this.regBasicRes_basic(desc)
    if (desc.videoUrl) this.fileMgr.reg(desc.videoUrl)
  }

  regBasicRes_exp(desc: expSceneDesc) {
    this.regBasicRes_basic(desc)
    // TODO
  }

  regBasicRes(...descs: arSceneDesc[]) {
    let _descs = descs.length ? descs : this.unready
    for (let desc of _descs) {
      if (ArSceneMgr.descIsName(desc)) continue
      if (ArSceneMgr.descIsModel(desc))
        this.regBasicRes_model(desc)
      else if (ArSceneMgr.descIsVideo(desc))
        this.regBasicRes_video(desc)
      else if (ArSceneMgr.descIsExp(desc))
        this.regBasicRes_exp(desc)
    }
  }

  async initBasicRes() {
    await this.markerMgr.init()
    await this.modelMgr.init()
    await this.matMgr.init()
    await this.fileMgr.init()
  }

  reg(desc: arSceneDesc) {
    if (ArSceneMgr.descIsName(desc)) return
    super.reg(desc)
  }

  static instance = new ArSceneMgr()
}

export default ArSceneMgr