import ArScene from './ArScene'
import ArSceneType from './ArSceneType'
import FileMgr from './FileMgr'
import MarkerMgr from './MarkerMgr'

class ArVideoScene extends ArScene {

  videoUrl = ''

  constructor(marker?: marker) {
    super(marker)
    this.add(this.camera)
    this.sceneType = ArSceneType.video
  }

  static fromDesc(
    desc: videoSceneDesc,
    markerMgr = MarkerMgr.instance,
    fileMgr = FileMgr.instance,
  ): ArVideoScene {
    let scene = new ArVideoScene()
    scene.setWithDesc(desc, markerMgr)
    let url = fileMgr.getBlobUrl(desc.videoUrl)
    if (!url) throw `Can't find video: ${url}`
    scene.videoUrl = url
    return scene
  }
}

export default ArVideoScene