import './ar-js'
import { WebGLRenderer } from 'three'
import { EventEmitter } from "events"
import ArScene from './ArScene'
import ProcessEvent from './ProcessEvent'
import ArModelScene from './ArModelScene'

class ArRender extends WebGLRenderer {

  private _arContext: THREEx.ArToolkitContext
  private _arSource: THREEx.ArToolkitSource
  private _clearNowScene = this._setNowScene.bind(this)
  private _contextReady = false
  private _looper_binded = this._looper.bind(this)
  private _nowScene?: ArScene
  private _onResize_binded = this._onResize.bind(this)
  private _scenes: Set<ArScene> = new Set([])
  private _timerNewNowScene?= 0

  events = new EventEmitter()
  funcs: tickFunc[] = []
  hidden = false
  lastTime = 0
  nowSceneActrual?: ArScene
  running = false

  get nowScene() {
    return this._nowScene
  }

  constructor(debugSource?: arSourceDesc) {
    super({ antialias: true, alpha: true })
    window.addEventListener(
      'arjs-nft-loaded', ev => this.events.emit('nftLoaded')
    )
    this.setClearAlpha(0)
    this.domElement.classList.add("filled")
    this._arSource = new THREEx.ArToolkitSource(
      debugSource || { sourceType: "webcam" }
    )
    this._arSource.init(this._onResize_binded)
    window.addEventListener("resize", this._onResize_binded)
    this._arContext = new THREEx.ArToolkitContext({
      cameraParametersUrl:
        'https://cdn.jsdelivr.net/gh/AR-js-org/AR.js@3.1.0' +
        '/data/data/camera_para.dat',
      detectionMode: "mono"
    })
  }

  async init() {
    if (this._contextReady) return
    return new Promise<void>(resolve =>
      this._arContext.init(() => {
        for (let scene of this._scenes)
          this._sceneToContext(scene, this._arContext)
        this.events.emit('contextReady')
        this._contextReady = true
        resolve()
      })
    )
  }

  private _firstActivedScene() {
    for (let scene of this._scenes)
      if (scene.camera.visible)
        return this.nowSceneActrual = scene
  }

  private _looper(time: number) {
    if (this.running) requestAnimationFrame(this._looper_binded)
    if (!this._arSource.ready) return
    if (this.hidden) return this.clear()
    this._arContext.update(this._arSource.domElement)
    if (!this.lastTime) this.lastTime = time - 1000 / 60
    let delta = time - this.lastTime

    for (let func of this.funcs) func(delta, time)

    this.nowSceneActrual = this._firstActivedScene()
    this._newNowScene()
    if (this._nowScene) {
      for (let func of this._nowScene.onTickFuncs)
        func(delta, time, this._nowScene)
      this.render(this._nowScene, this._nowScene.camera)
    }

    this.lastTime = time
  }

  private _newNowScene() {
    if (this.nowSceneActrual) {
      if (this._timerNewNowScene)
        clearTimeout(this._timerNewNowScene)
      this._timerNewNowScene = undefined
      if (this._nowScene === this.nowSceneActrual) return
      this._setNowScene(this.nowSceneActrual)
    }
    else if (this._timerNewNowScene === undefined)
      this._timerNewNowScene = setTimeout(this._clearNowScene, 2000)
  }

  private _onResize() {
    this._arSource.onResizeElement()
    this._arSource.copyElementSizeTo(this.domElement)
    if (this._arContext.arController)
      this._arSource.copyElementSizeTo(
        this._arContext.arController.canvas
      )
  }

  private _sceneToContext(
    scene: ArScene,
    context: THREEx.ArToolkitContext
  ) {
    if (!scene.marker) return
    scene.camera.projectionMatrix.copy(context.getProjectionMatrix())
    return new THREEx.ArMarkerControls(context, scene.camera, {
      type: "nft",
      descriptorsUrl: scene.marker,
      changeMatrixMode: "cameraTransformMatrix",
      smoothCount: 60,
      smoothTolerance: 1,
      smoothThreshold: 5,
      smooth: true,
      size: 0.16
    })
  }

  private _setNowScene(scene?: ArScene) {
    let lastScene = this._nowScene
    if (lastScene) lastScene.visible = false

    if (scene) {
      scene.visible = true
      if (scene instanceof ArModelScene) {
        let s = 300 / scene.size
        let content = scene.getObjectByName('content')
        content && content.scale.set(s, s, s)
      }
    }
    this._nowScene = scene
    this.clear()
    this.events.emit('sceneChanged', scene, lastScene)
  }

  addScene(scene: ArScene) {
    this._scenes.add(scene)
    if (this._contextReady)
      this._sceneToContext(scene, this._arContext)
  }

  async loadScenes(scenes: ArScene[], processCB?: processCB) {
    if (!this._contextReady) throw 'Need contextReady first!'
    let n = 0
    let total = scenes.length
    return new Promise<void>(resolve => {
      let func = () => {
        processCB && processCB(new ProcessEvent(
          'ArRender.ArStarting', undefined, n += 1, total))
        if (n !== total) return
        this.events.off('nftLoaded', func)
        resolve()
      }
      this.events.on('nftLoaded', func)
      for (let scene of scenes) {
        if (!scene.marker) func()
        this.addScene(scene)
      }
    })
  }

  domTo(container: HTMLElement) {
    this.setSize(container.offsetWidth, container.offsetHeight)
    container.appendChild(this.domElement)
    setInterval(() => this._onResize(), 1000)
  }

  hide() {
    this.hidden = true
    this.pause()
  }

  pause() {
    this.running = false
    if (this._timerNewNowScene) {
      clearTimeout(this._timerNewNowScene)
      this._timerNewNowScene = undefined
    }
  }

  removeScene(scene: ArScene) {
    this._scenes.delete(scene)
  }

  start() {
    if (this.running) return
    this.hidden = false
    this.running = true
    requestAnimationFrame(this._looper_binded)
  }

}

export default ArRender