import { Material } from 'three'

import ResourceMgr from './ResourceMgr'

import TexMgr from './TexMgr'

class MatMgr extends ResourceMgr<matDesc, Material> {
  texMgr = TexMgr.instance

  regBasicRes(...descs: matDesc[]) {
    let _descs = descs.length ? descs : this.unready
    for (let desc of _descs) {
      if (desc instanceof Material) continue
      if (!desc.matClass && !desc.init)
        throw `Error MatDesc: ${JSON.stringify(desc)}`
      if (desc.params)
        for (let prop of Object.getOwnPropertyNames(desc.params))
          if (prop === 'map' || prop.endsWith('Map'))
            this.texMgr.reg(desc.params[prop] as texDesc)
    }
  }

  async initBasicRes() {
    await this.texMgr.init()
  }

  async make(desc: matDesc, basicReadied?: boolean) {
    let inst = this.get(desc)
    if (inst) return inst
    if (desc instanceof Material) {
      this.set(desc, desc)
      return desc
    }
    if (!basicReadied) {
      this.regBasicRes(desc)
      await this.initBasicRes()
    }
    let params: any = {}
    if (desc.params)
      for (let prop of Object.getOwnPropertyNames(desc.params))
        if (prop === 'map' || prop.endsWith('Map'))
          params[prop] = this.texMgr.get(desc.params[prop])
        else params[prop] = desc.params[prop]
    let mat: Material
    if (desc.matClass) mat = new (<any>desc.matClass)(params)
    else if (desc.init) mat = desc.init(params)
    else throw `Error MatDesc: ${JSON.stringify(desc)}`
    this.set(desc, mat)
    return mat
  }

  reg(desc: matDesc) {
    if (desc instanceof Material)
      this.set(desc, desc)
    else super.reg(desc)
  }

  static instance = new MatMgr()
}

export default MatMgr